let that;
import {
	updateCo,
	searchCompany
} from '@/axios/index.js'
export default {
	data() {
		return {
			imgurl: '',
			companyInfo: {}
		}
	},

	mounted() {
		that = this;
		that.getCompany()
		console.log(that.companyInfo)
		uni.$on('getCompany',that.getCompany)
	},
	methods: {
		toStoreInfo: function() {
			that.$Router.push({name:'store_info'})
		},
		toServiceInfo: function() {
			uni.navigateTo({
				url: '../service_info/service_info'
			})
		},
			
		toLogin: function() {
			uni.navigateTo({
				url: '../login/login'
			})
		},
		toAttestAtion: function() {
			that.$Router.push({name:'attestAtion'})
		},
		upLogo: function() {
			uni.chooseImage({
			    count: 1, 
			    sizeType: ['original', 'compressed'],
			    sourceType: ['album'],
			    success: function (res) {
			        console.log(res.tempFilePaths[0]);
					that.imgurl = res.tempFilePaths[0]
					uni.showModal({
					    title: '提示',
					    content: '是否更改店铺LOGO',
					    success: function (res) {
					        if (res.confirm) {
								that.updateIcon(that.imgurl)
					        } else if (res.cancel) {
					            that.imgurl = that.companyInfo.imgurl 
					        }
					    }
					});
			    }
			});
		},
		toFeedBack:function(){
			that.$Router.push({name:'feedBack'})
		},
		updateIcon: function(url) {
			updateCo(url,{
				id: that.companyInfo.id
			}).then((res)=>{
				if(JSON.parse(res).ret == 0) {
					uni.$emit('getCompany')
					uni.showToast({
					    title: '更改成功',
					    duration: 2000
					});
				} else {
					console.log(res)
					that.imgurl = that.companyInfo.imgurl 
					uni.showToast({
					    title: '更改失败，请重试',
					    duration: 2000
					});
				}
			})
		},
		getCompany: function() {
			searchCompany().then(r=>{
				console.log(r)
				if(r.ret == 0) {
				  that.companyInfo = r.info.list[0]
				  that.imgurl = that.companyInfo.imgurl
				}
			})
		},
		toSettings: function() { //设置
			that.$Router.push({name:'settings'})
		}
	}
}
